.aside {
    position: fixed;
    left: 0;
    top: 0;
    background-color: transparent;
    border-right: 1px solid rgba(141, 50, 50, 0.05);
    padding: 2.5rem;
    width: 110px;
    min-height:  100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.nav_list{
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.nav_link {
    font-size: 1.5rem;
    color: var(-title-color);
    font-weight: var(--font-bold);
    transition: .3s;

}


/* sidebar.css */
.nav__link i {
  color: var(--navlink-color); /* Changes all icons inside .nav__link to white */
}



.nav__link:hover{
    color: hsl(43, 100%, 68%);
}
.copyright {
    color: hsl(245,15%, 65%);
    font-size: var(--small-font-size);
    transform: rotate(-180deg);
    writing-mode: vertical-rl;

}

