.blog {
    padding-bottom: 2rem;
  }
  
  .blog.container.section {
    padding-bottom: 2rem;
  }
  
  .blog__container {
    margin-bottom: 2rem;
    display: grid;
    gap: 1.5rem;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
  
  .blog__card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .blog__card:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
  }
  
  .blog__thumb {
    position: relative;
    overflow: hidden;
  }
  
  .blog__thumb img {
    width: 100%;
    height: auto;
    display: block;
  }
  
  .blog__category {
    position: absolute;
    top: 1rem;
    left: 1rem;
    font-size: 0.875rem;
    font-weight: bold;
    padding: 0.25rem 0.5rem;
    text-align: center;
    border-radius: 5px;
    background-color: #f0f0f0;
    min-width: 100px;
  }
  
  .blog__details {
    padding: 1rem;
  }
  
  .blog__title {
    font-size: 1.125rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
    color: #333;
  }
  
  .blog__meta {
    font-size: 0.875rem;
    color: #666;
  }
  
  .blog__dot {
    margin: 0 0.25rem;
  }
  
  /* Media Queries */
  @media (max-width: 768px) {
    .blog__container {
      grid-template-columns: 1fr;
      gap: 1rem;
    }
  
    .blog__thumb img {
      height: 200px;
      object-fit: cover;
    }
  }
  
  @media (max-width: 480px) {
    .blog__container {
      gap: 0.5rem;
    }
  
    .blog__category {
      font-size: 0.75rem;
      padding: 0.2rem 0.4rem;
      min-width: 80px;
    }
  
    .blog__title {
      font-size: 1rem;
    }
  
    .blog__meta {
      font-size: 0.75rem;
    }
  }
  

  .blog__read-now {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0); /* Initially transparent */
    color: #fff;
    font-size: 1.25rem;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 2;
    opacity: 0; 
    transition: background-color 0.5s ease, opacity 0.5s ease; /* Smooth transition for overlay */
  }
  
  .blog__card:hover .blog__read-now {
    opacity: 1; /* Fade in on hover */
    background-color: rgba(0, 0, 0, 0.7); /* Darker background on hover */
  }

  