.home {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro {
    max-width: 540px;
    text-align: center;

}

.home__img {
    margin-bottom: 1.5rem;
    background-repeat: no-repeat;
    width:200px;
    height:220px;
    background-size: cover;
    background-position: center;
    border: solid 3px rgb(0,0,0, 0.5) ;
    box-shadow: inset 0 0 0 9px rgba(27, 209, 58, 0.3);
    order: 1;
    justify-self: center;
    animation: profile__animate 8s ease-in-out infinite 0s;
}

@keyframes profile__animate {

    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }
    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}
.home__name {
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.home__socials {
    display:flex;
    column-gap: 1.75rem;
    margin: 1.5rem 0;
}

.home__social-link {
    color: var(--title-color);
    font-size: 1.5rem;
    transition: .3s;
}

.home__social-link:hover {
    color: hsl(43, 100%, 68%);
}

.scroll__down {
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}

.home__scroll-name {
    color: white;
    font-size: var(--small-font-size);
}

.mouse {
    border: 2px solid #454360;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
}

@keyframes ani-mouse {
    0%{
        top: 29%;
    }

    15%{
        top: 50%;
    }

    50%{
        top: 50%;
    }
    100%{
        top: 29%;
    }



}
.wheel {
    background-color: black;
    border-radius:  100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 1s linear infinite;
}